body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2f3542 !important;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.GameOfLife-Controls {
  text-align: center;
  padding-top: 1rem;
  margin-bottom: 1.5rem;
}

.GameOfLife-Controls > * {
  margin-left: 0.5rem;
}

.Speed-Slider {
  display: inline-block;
  width: 120px;
  vertical-align: middle;
}

.GameOfLife-Controls .rangeslider-horizontal {
  height: 10px;
  border-radius: 0;
  background-color: #fff;
  box-shadow: none;
}
.rangeslider__labels {
  color: #fff;
}

.GameOfLife-Controls .rangeslider-horizontal .rangeslider__fill {
  background-color: #F97F51;
  border-radius: 0;
  box-shadow: none;
}

.GameOfLife-Controls .rangeslider-horizontal .rangeslider__handle {
 width: 12px;
 border-radius: 0;
 box-shadow: none;
}

.GameOfLife-Controls .rangeslider-horizontal .rangeslider__handle:after {
  width: 0;
  height: 0;
}

.dropdown {
  display: inline-block;
}
.GameOfLife-Grid {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  border-top: 1px solid #666;
  border-left: 1px solid #666;
  box-sizing: content-box;
  margin: auto;
  margin-bottom: 1.5rem;
}

.GameOfLife-Row{
  line-height: 12px;
  
}

.GameOfLife-Cell {
  flex: 0 0 12px;
  width: 12px;
  border-right: 1px solid #666;
  border-bottom: 1px solid #666;
  height: 12px;
  display: inline-block;
}

.GameOfLife-Cell:hover {
  background-color: #EAB543;
}

.GameOfLife-Cell-Alive {
  background-color: #55E6C1;

}
.GameOfLife h3,
.GameOfLife h1 {
  color: #fff;
  text-align: center;
}
